.main-menu__wrapper {
  position: fixed;
  right: 0;
  height: 100%;
  width: 250px;
  background: #fff;
  z-index: 9999;
  box-shadow: 0 10px 21px rgba(0, 0, 0, 0.15);
  transform: translateX(250px);
  transition: all 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
}

.main-menu__wrapper.active {
  visibility: visible;
  opacity: 1;
}

.main-menu__wrapper.visible {
  transform: translateX(0px);
}

.main-menu__inner {
  padding-top: 30px;
  padding-right: 50px;
  text-align: right;
  position: relative;
}

.main-menu__control {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  left: 1px;
  bottom: 100px;
  border: 2.4em solid transparent;
  border-color: transparent transparent #fff #fff;
  transform-origin: 0 0;
  transform: rotate(45deg);
  box-shadow: -5px 5px 5px 0 rgba(0, 0, 0, 0.04);
}

.main-menu__close {
  position: absolute;
  top: 5px;
  left: -25px;
  transform: rotate(-45deg);
}

.main-menu__close span {
  display: block;
  height: 4px;
  border-radius: 2px;
  background: #707070;
  width: 24px;
  margin-bottom: 3px;
  opacity: .7;
}

.main-menu__close:hover span {
  opacity: 1;
}

.main-menu__close.visible span {
  margin: 0;
  position: relative;
  left: -5px;
}

.main-menu__close.visible span:last-child {
  display: none;
}

.main-menu__close.visible span:first-child {
  transform: rotate(45deg);
  top: 4px;
}

.main-menu__close.visible span:nth-child(2) {
  transform: rotate(-45deg);
}

.menu__logo {
